import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Layout from '@/components/Layout'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      component: Layout,
      children:[
        {
          path:'/',
          component:Home,
          name:'Home',
          meta: {
            fullLayout: true
          }
        },
        {
          path: '/housetype',
          component: () => import( /* webpackChunkName: "HouseType" */ '@/components/HouseType.vue'),
          name: 'HouseType',
          meta: {
            fullLayout: true
          }
        },
        {
          path: '/overview',
          component: () => import( /* webpackChunkName: "Overview" */ '@/components/Overview.vue'),
          name: 'Overview',
          meta: {
            title: "사업개요"
          }
        },
        {
          path: '/environment',
          component: () => import( /* webpackChunkName: "Environment" */ '@/components/Environment.vue'),
          name: 'Environment',
          meta: {
            title: "입지환경"
          }
        },
        {
          path: '/interest',
          component: () => import( /* webpackChunkName: "Interest" */ '@/components/Interest.vue'),
          name: 'Interest',
          meta: {
            title: "관심 고객 추가"
          }
        },
        {
          path: '/privacy',
          component: () => import( /* webpackChunkName: "Privacy" */ '@/components/Privacy.vue'),
          name: 'Privacy',
          meta: {
            title: "개인정보취급"
          }
        },
        {
          path: '/terms',
          component: () => import( /* webpackChunkName: "Terms" */ '@/components/Terms.vue'),
          name: 'Terms',
          meta: {
            title: "이용약관"
          }
        },
        {
          path: '/gonggo',
          component: () => import( /* webpackChunkName: "Gonggo" */ '@/components/Gonggo.vue'),
          name: 'Gonggo',
          meta: {
            title: "모집공고"
          }
        },
        {
          path: '/premium1',
          component: () => import( /* webpackChunkName: "Premium1" */ '@/components/Premium1.vue'),
          name: 'Premium1',
          meta: {
            title: "입지 프리미엄"
          }
        },
        {
          path: '/premium2',
          component: () => import( /* webpackChunkName: "Premium2" */ '@/components/Premium2.vue'),
          name: 'Premium2',
          meta: {
            title: "설계 프리미엄"
          }
        },
        {
          path: '/danji',
          component: () => import( /* webpackChunkName: "Danji" */ '@/components/Danji.vue'),
          name: 'Danji',
          meta: {
            title: "단지 배치도"
          }
        },
        {
          path: '/community',
          component: () => import( /* webpackChunkName: "Community" */ '@/components/Community.vue'),
          name: 'Community',
          meta: {
            title: "커뮤니티"
          }
        },
        {
          path: '/viewmap',
          component: () => import( /* webpackChunkName: "Viewmap" */ '@/components/Viewmap.vue'),
          name: 'Viewmap',
          meta: {
            title: "조감도"
          }
        }
      ]
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return {
        selector: to.hash
      };
    } else {
      return {
        x: 0,
        y: 0
      };
    }
  },
    mode:'history'
})
router.beforeResolve((to, from, next) => {
  if (to.name) {
    NProgress.start()
  }
  next()
})

router.afterEach((to, from) => {
  NProgress.done()
})
export default router;


