<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      app disable-resize-watcher temporary
      :width="$vuetify.breakpoint.smAndDown ? '100%' : 400"
      color="primary text--darken-5"
      dark
    >
    <!-- :src="require('../assets/images/nav_bg.png')" -->
        <v-img
      :src="require('../assets/images/nav_bg.jpg')"
      height="200px"
      dark
    >
      <v-row class="fill-height">
        <div></div>
        <v-spacer />
        <v-card-title class="white--text pr-3">
          <div class="display-1 pr-3"></div>
        </v-card-title>
      </v-row>
    </v-img>
    <v-list>
      <v-list-item>
        <v-list-item-action>
          <v-icon>mdi-cube</v-icon>
        </v-list-item-action>
        <v-list-item-content>
          <v-list-item-title class=" title">보라매&nbsp;&nbsp;센트럴바움</v-list-item-title>
        </v-list-item-content>
        <v-list-item-icon @click.stop="drawer = false"><v-icon>mdi-close</v-icon></v-list-item-icon>
      </v-list-item>
        <v-list-group
          v-for="menu in menus"
          :key="menu.title"
          v-model="menu.active"
          :prepend-icon="menu.icon"
          no-action
          active-class="white--text"
        >
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title v-text="menu.title"></v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item active-class="white--text"
            v-for="child in menu.submenus"
            :key="child.title"
            :to="child.path"
          >
              <v-list-item-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title v-text="child.title">
                </v-list-item-title>
              </v-list-item-content>
          </v-list-item>
        </v-list-group>
        </v-list>
    </v-navigation-drawer>
    <v-app-bar
      app
      clipped-left
      color="primary"
    >
     <v-app-bar-nav-icon @click.stop="drawer = !drawer" />

      <v-toolbar-title justify="center" @mouseover="hoverTitle" @mouseleave="leaveTitle">
        <!-- <a href="/" class="white--text" style="text-decoration: none"><v-icon>mdi-truck</v-icon>&nbsp;ShipIT</a> -->
        <router-link to="/" class="white--text d-flex flex-nowrap" style="text-decoration: none">
          <div class="d-none d-sm-flex" >보라매&nbsp;&nbsp;</div><v-icon dark>{{titleIcon}}</v-icon><div> 센트럴바움</div>
        </router-link>
<!-- 
        <a to="/" class="white--text d-flex flex-nowrap" style="text-decoration: none" @mouseover="hoverTitle" @mouseleave="leaveTitle">
          <div class="d-none d-sm-flex secondary--text" >보라매&nbsp;&nbsp;</div><v-icon>{{titleIcon}}</v-icon><div> 센트럴바움</div>
        </a> -->
      </v-toolbar-title>
      <v-spacer />
      <v-btn outlined dark @mouseover="hoverAccount" @mouseleave="leaveAccount" to="/interest">
        <v-icon>{{accountIcon}}</v-icon><span class="d-none d-sm-flex pl-2">관심고객 등록</span>
      </v-btn>
      <v-btn outlined dark class="ml-2" href="tel:02-849-9015">
        <v-icon>mdi-face-agent</v-icon><span class="d-none d-sm-flex pl-2">02-849-9015</span>
      </v-btn>
    </v-app-bar>

    <v-content>
      <v-bottom-navigation
        :value="activeBtn"
        horizontal
      >
        <v-menu v-for="(menu,index) in menus" :key="index" open-on-hover offset-y transition="slide-y-transition">
          <template v-slot:activator="{ on }">
              <v-btn v-on="on" class="subtitle-1">
                {{menu.title}}
              </v-btn>
          </template>
          <v-list>
            <v-list-item
              v-for="(submenu, index) in menu.submenus"
              :key="index"
              :to="submenu.path"
            >
              <v-list-item-title class="subtitle-2">{{ submenu.title }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-bottom-navigation>
    </v-content>

  <template v-if="$route.meta && $route.meta.fullLayout">
    <v-card
    flat
    tile
    width="100%"
    class="white--text text-center mb-3"
    >
          <router-view/>
    </v-card>
  </template>
  <template v-else>
    <div>
      <base-header :title="$route.meta.title" :mainImage="require('../assets/images/bg.jpg')" />
      <v-container fluid>
        <div class="col-md-12 col-sm-12 col-xs-12">
          <router-view/>
        </div>
      </v-container>
    </div>
  </template>

    <v-footer
      padless
    >
      <v-card
        tile
        width="100%"
        class="secondary white--text text-center"
      >
        <v-card-text class="text-center">
          <div class="d-flex flex-nowrap text-center justify-center">
            <v-btn class="white--text mr-2" tile outlined to="/privacy">
              <v-icon>mdi-clipboard-account-outline</v-icon>
              <span class="hidden-sm-and-downs">개인정보취급</span>
            </v-btn>
            <v-btn class="white--text mr-2" tile outlined to="/terms">
              <v-icon>mdi-calendar</v-icon>
              <span class="hidden-sm-and-downs">이용약관</span>
          </v-btn>
            <v-btn class="white--text" tile outlined :href="`mailto:${adminEmail}`" target="_blank">
              <v-icon>mdi-email</v-icon>
              <span class="hidden-sm-and-downs">문의</span>
            </v-btn>
          </div>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-text class="white--text text-left pl-6">
          <span>
            보라매 센트럴 지역주택조합<br />
            사업자번호 : xxxx
          </span>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-text class="white--text">
          {{ new Date().getFullYear() }} — <strong>Copyright ⓒYA All rights reserved.</strong>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>
<script>
import BaseHeader from '@/components/BaseHeader';
    export default {
        data () {
            return {
              drawer: false,
              group: null,
                activeBtn: 1,
                titleIcon: "mdi-cube-outline",
                accountIcon: "mdi-account-plus-outline",
                menus: [
                  {
                    title: "사업안내",
                    icon: "mdi-information",
                    model: false,
                    submenus: [
                      {
                        title: "사업개요",
                        path: "/overview",
                        icon: "mdi-information"
                      },
                      {
                        title: "입지환경",
                        path: "/environment",
                        icon: "mdi-map-marker"
                      },
                      {
                        title: "모집공고",
                        path: "/gonggo",
                        icon: "mdi-file-cloud-outline"
                      }
                    ]
                  },
                  {
                    title: "프리미엄",
                    icon: "mdi-google-circles-extended",
                    model: false,
                    submenus: [
                      {
                        title: "입지 프리미엄",
                        path: "/premium1",
                        icon: "mdi-google-circles-extended"
                      },
                      {
                        title: "설계 프리미엄",
                        path: "/premium2",
                        icon: "mdi-globe-model"
                      }
                    ]
                  },
                  {
                    title: "단지안내",
                    icon: "mdi-scatter-plot-outline",
                    model: false,
                    submenus: [
                      {
                        title: "단지 배치도",
                        path: "/danji",
                        icon: "mdi-scatter-plot-outline"
                      },
                      {
                        title: "커뮤니티",
                        path: "/community",
                        icon: "mdi-nfc-tap"
                      },
                      {
                        title: "조감도",
                        path: "/viewmap",
                        icon: "mdi-view-compact-outline"
                      }
                    ]
                  },
                  {
                    title: "분양안내",
                    icon: "mdi-file-chart",
                    model: false,
                    submenus: [
                      {
                        title: "공급안내",
                        path: "/salesinfo",
                        icon: "mdi-file-chart"
                      }
                    ]
                  },
                  {
                    title: "세대안내",
                    icon: "mdi-floor-plan",
                    model: false,
                    submenus: [
                      {
                        title: "평면정보",
                        path: "/housetype",
                        icon: "mdi-floor-plan"
                      }
                    ]
                  },
                ],
                adminEmail: 'yaglobal@naver.com'
            }
        },
        watch: {
          group () {
            this.drawer = false
          },
        },
        methods: {
          hoverTitle: function(){
            this.titleIcon = 'mdi-cube'
          },
          leaveTitle: function(){
            this.titleIcon = 'mdi-cube-outline'
          },
          hoverAccount: function(){
            this.accountIcon = 'mdi-account-plus'
          },
          leaveAccount: function(){
            this.accountIcon = 'mdi-account-plus-outline'
          },
        },
        components: {
          BaseHeader
        }
    }
</script>



